.loading{height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.loading__heading{text-align:center}.loading__spinner{width:4.375rem;text-align:center}.loading__bounce{width:1.125rem;height:1.125rem;background-color:#333;border-radius:100%;display:inline-block;-webkit-animation:sk-bouncedelay 1.4s infinite ease-in-out both;animation:sk-bouncedelay 1.4s infinite ease-in-out both}.loading__bounce:nth-child(1){-webkit-animation-delay:-0.32s;animation-delay:-0.32s}.loading__bounce:nth-child(2){-webkit-animation-delay:-0.16s;animation-delay:-0.16s}@-webkit-keyframes sk-bouncedelay{0%,80%,100%{-webkit-transform:scale(0)}40%{-webkit-transform:scale(1)}}@keyframes sk-bouncedelay{0%,80%,100%{-webkit-transform:scale(0);transform:scale(0)}40%{-webkit-transform:scale(1);transform:scale(1)}}
